header {
  width: 100%;
  background-image: url(../img/Rectangle\ 765.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 120px;
}
header nav {
  padding: 67px 0px;
}
header nav ul {
  display: flex;
  justify-content: right;
  align-items: center;
  margin-top: 40px;
}
@media (max-width: 991px) {
  header nav ul {
    flex-direction: column;
    margin-top: 20px;
    width: 100%;
  }
}
header nav ul li {
  margin: 0px 20px;
}
@media (max-width: 991px) {
  header nav ul li {
    padding-bottom: 5px;
    width: 100%;
    padding-left: 5px;
    padding-right: 5px;
    margin: 5px 0px;
  }
}
header nav ul a {
  font-size: 15px;
  font-weight: 500;
  line-height: 21px;
  letter-spacing: 1px;
  text-align: left;
  color: white;
}
header nav ul a:hover {
  cursor: pointer;
  transition: 1s;
  color: #BAED29;
}
header nav .bars {
  width: 30px;
  height: 30px;
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  border: 2px solid black;
}
header nav .bars:hover {
  border-color: #BAED29;
  color: #BAED29;
}
header nav .bars .fa {
  font-size: 16px;
}
header nav .flex {
  text-align: center;
}
header nav .flex h6 {
  font-size: 17px;
  font-weight: 300;
  line-height: 33px;
  letter-spacing: 1px;
  margin-top: 5px;
  color: white;
}
header nav .flex p {
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  letter-spacing: 0.3em;
  color: white;
}
header .headerTitle {
  width: 100%;
  padding-top: 140px;
  padding-bottom: 120px;
}
header .headerTitle .box {
  width: 100%;
  text-align: left;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid #EEEEEE;
  box-shadow: 0px 34px 135px rgba(0, 0, 0, 0.1), 0px 13.0963px 43px rgba(0, 0, 0, 0.0242963), 0px 2.77037px 11px rgba(0, 0, 0, 0.0157037);
  backdrop-filter: blur(30px);
  border-radius: 24px;
  padding: 0px 40px;
  padding-bottom: 50px;
}
header .headerTitle .box h5 {
  padding-top: 50px;
  font-size: 24px;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: -1px;
  color: white;
}
@media (max-width: 767px) {
  header .headerTitle .box h5 {
    font-size: 20px;
  }
}
header .headerTitle .box p {
  font-size: 18px;
  font-weight: 400;
  line-height: 32px;
  color: white;
  margin-top: 6px;
}
@media (max-width: 767px) {
  header .headerTitle .box p {
    font-size: 15px;
    line-height: 25px;
  }
}
header .headerTitle .box form {
  padding-top: 30px;
  display: flex;
  flex-direction: column;
}
header .headerTitle .box form input {
  border: none;
  background: #FFFFFF;
  border-bottom: 1px solid #979797;
  border-radius: 12px;
  padding: 20px 0px;
  padding-left: 30px;
  margin: 15px 0px;
}
header .headerTitle .box form input:focus {
  outline: none;
}
header .headerTitle .box form input::placeholder {
  font-size: 18px;
  font-weight: 400;
  line-height: 32px;
}
@media (max-width: 767px) {
  header .headerTitle .box form input::placeholder {
    line-height: 15px;
  }
}
header .headerTitle .box button {
  font-size: 18px;
  font-weight: 500;
  line-height: 21px;
  color: black;
  background-color: #BAED29;
  border: none;
  padding: 20px 50px;
  margin-top: 30px;
  border-radius: 50px;
}
header .headerTitle .box button:hover {
  cursor: pointer;
  transition: 1s;
  background-color: white;
  color: black;
  border: none;
}
header .headerTitle .left {
  width: 100%;
  text-align: left;
}
@media (max-width: 991px) {
  header .headerTitle .left {
    margin-top: 50px;
  }
}
header .headerTitle .left h1 {
  font-size: 80px;
  font-weight: 300;
  line-height: 96px;
  letter-spacing: -3px;
  text-align: left;
  color: white;
}
@media (max-width: 767px) {
  header .headerTitle .left h1 {
    font-size: 30px;
    line-height: 40px;
    letter-spacing: 1px;
  }
}
header .headerTitle .left p {
  font-size: 18px;
  font-weight: 400;
  line-height: 32px;
  color: white;
}
@media (max-width: 767px) {
  header .headerTitle .left p {
    font-size: 15px;
    line-height: 25px;
    margin-top: 10px;
  }
}
header .headerTitle .left .car {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid #EEEEEE;
  box-shadow: 0px 34px 135px rgba(0, 0, 0, 0.1), 0px 13.0963px 43px rgba(0, 0, 0, 0.0242963), 0px 2.77037px 11px rgba(0, 0, 0, 0.0157037);
  backdrop-filter: blur(30px);
  border-radius: 24px;
  padding: 20px 20px;
  margin-top: 40px;
}
@media (max-width: 991px) {
  header .headerTitle .left .car {
    text-align: center;
  }
}
header .headerTitle .left .car img {
  width: 150px;
  height: 150px;
}
@media (max-width: 767px) {
  header .headerTitle .left .car img {
    width: 100%;
    margin: 10px 0px;
    padding: 0;
  }
}
