  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: white;
  color: #BAED29;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 767px) {
